.buttonWrapper {
    height: 50px;
    border: 4px solid darkblue;
    margin: 10px 40px 10px 40px;
    text-align: center;
    font-size: 34px;
    border-radius: 10px;
    padding: 0 15px;
}

@media screen and (max-width: 930px) {
    .buttonWrapper {
        margin: 10px 10px 10px 10px;
    }
}